<template>
	<view class="picker-wrap flex-between c-center">
		<picker class="month-picker" mode="multiSelector" :range='range' :value="values" @change="onChange" @click="onClickPicker" @cancel="onCancelPicker">
			<view class="values flex c-center" v-if="values && values.length > 0">
				<text>{{ `${range[0][values[0]]}年${range[1][values[1]]}月` }}</text>
				<text :class="['three-angle', { reverse: isPickerShow }]"></text>
			</view>
			<view class="placehodler" v-else>
				请选择时间
			</view>
		</picker>
	</view>
</template>

<script>
	
	const NOW = new Date()
	const YEAR = NOW.getFullYear()
	const MONTH = NOW.getMonth() + 1
	export default {
		props: {
			value: {
				type: Array,
				default: () => ([YEAR, MONTH])
			},
			start: {
				type: Number,
				default: 2010
			},
			end: {
				type: Number,
				default: 2100
			}
		},
		data() {
			return {
				range: [],
				values: [],
				pickering: false,
				statusRanges: ['未结', '已结'],
				currentStatus: 0,
				isPickerShow: false
			}
		},
		created() {
			this.createRange()
			this.setValues()
		},
		methods: {
			onClickPicker() {
				this.isPickerShow = true
			},
			onCancelPicker() {
				this.isPickerShow = false
			},
			onPickerStatus(e) {
				this.currentStatus = e.detail.value * 1
				this.$emit('change')
			},
			onChange(e) {
				this.values = e.detail.value
				this.$emit('change')
				this.onCancelPicker()
			},
			getValue() {
				const [year, month] = this.values.map((value, index) => {
					return this.range[index][value]
				})
				return {
					preOrderDateMonth: `${year}-${month}`
				}
			},
			setValues() {
				const value = this.value
				const range = this.range
				this.values = value.map((value, index) => {
					return range[index].findIndex(num => num === value)
				})
			},
			createRange() {
				this.range = [this.createNums(this.start, this.end), this.createNums(1, 13)]
			},
			createNums(start, end) {
				return Array.apply(null, {
					length: end - start
				}).map((_, index) => start + index)
			}
		}
	}
</script>

<style lang="scss" scoped>
	
.picker-wrap {
	display: inline-block;
	@extend .shadow-box;
	line-height: 72upx;
	border-radius: 72upx;
	padding: 0 24upx;
	z-index: 1;
	font-size: 28upx;
	
	.three-angle {
		border-top: 10upx solid #666;
		border-left: 10upx solid transparent;
		border-right: 10upx solid transparent;
		margin-left: 10upx;
		
		transition: all .3s;
		
		&.reverse {
			transform: rotate(180deg);
		}
	}
}
</style>
